import React from "react";
import { Link } from "react-router-dom";
import avatar from "../../images/avatar.jpg";
import { Link as LinkScroll, animateScroll as scroll } from "react-scroll";

import GoToTop from '../shared/GoToTop.js';

import chessBoard from "./ChessAIProject.PNG";

import chessCode from "./chessAIMain.cpp";


const Project2 = () => {
    return (
        <>
            <body class="is-preload">

                <div id="header">
                    <div className="top">

                        <div id="logo">
                            <span className="image avatar48"><img src={avatar} alt="" /></span>
                            <Link to="/react-gh-pages/">
                                <h1 id="title">Tristan Johnson</h1>
                            </Link>
                            <p>Software Developer</p>

                        </div>

                        <nav id="nav">
                            <ul>
                                <li>
                                    <Link to="/react-gh-pages/">
                                        <span className="solid">Home</span>
                                    </Link>
                                </li>
                                <li>
                                    <Link to="/react-gh-pages/project1">
                                        <span className="solid">Project 1</span>
                                    </Link>
                                </li>
                                <li>
                                    <Link to="/react-gh-pages/project3">
                                        <span className="solid">Project 3</span>
                                    </Link>
                                </li>
                                <li>
                                    <Link to="/react-gh-pages/project4">
                                        <span className="solid">Project 4</span>
                                    </Link>
                                </li>
                            </ul>
                        </nav>
                    </div>

                    <div className="bottom">
                        <ul className="icons">
                            <li><a href="#" className="icon brands fa-twitter"><span className="label">Twitter</span></a></li>
                            <li><a href="#" className="icon brands fa-facebook-f"><span className="label">Facebook</span></a></li>
                            <li><a href="#" className="icon brands fa-github"><span className="label">Github</span></a></li>
                            <li><a href="#" className="icon brands fa-dribbble"><span className="label">Dribbble</span></a></li>
                            <li><a href="#" className="icon solid fa-envelope"><span className="label">Email</span></a></li>
                        </ul>
                    </div>

                </div>

                <div id="main">



                    <section id="portfolio" class="two">
                        <div class="container">

                            <header>
                                <h2>Console Chess AI</h2>
                            </header>

                            <p>A simple, single threaded chess AI using the Minimax algorithm with alpha-beta pruning, rated at around 1600-1700. The AI uses no theory, it calculates best position from the very beginning. In its current state, simply download it and compile it in Visual Studio to play (it uses a Windows specific API right now but you can comment them out and compile with the compiler of your choice on Linux or Mac if you really wanted to). The file is available below the images!</p>

                        </div>
                    </section>


                    <div>
                            <img src = {chessBoard} class = "center" alt="console chess AI"/>
                    </div>

                    <a href = {chessCode}>Download Code</a>

                </div>

            </body>
            <GoToTop/>

        </>
    )
}

export default Project2;