import React from "react";
import { Link } from "react-router-dom";
import avatar from "../../images/avatar.jpg";
import { Link as LinkScroll, animateScroll as scroll } from "react-scroll";
import { Header, Button, Content, RouterLink } from "../shared/Styling.js";


import GoToTop from '../shared/GoToTop.js';

import voxels1 from "./voxel3DNoise1.PNG";
import voxels2 from "./voxel3DNoise2.PNG";
import voxels3 from "./VoxelRasterizer3.PNG";


import chunkScript from "./code/ChunkWithMesh.cs"
import chunksManagerScript from "./code/ChunksManager.cs"
import noiseComputeShader from "./code/NoiseComputeShader.compute"



const Project1 = () => {
    return (
        <>

            <body class="is-preload">

                <div id="header">
                    <div className="top">

                        <div id="logo">
                            <span className="image avatar48"><img src={avatar} alt="" /></span>
                            <Link to="/react-gh-pages/">
                                <h1 id="title">Tristan Johnson</h1>
                            </Link>
                            <p>Software Developer</p>

                        </div>

                        <nav id="nav">
                            <ul>
                                <li>
                                    <Link to="/react-gh-pages/">
                                        <span className="solid">Home</span>
                                    </Link>
                                </li>
                                <li>
                                    <Link to="/react-gh-pages/project2">
                                        <span className="solid">Project 2</span>
                                    </Link>
                                </li>
                                <li>
                                    <Link to="/react-gh-pages/project3">
                                        <span className="solid">Project 3</span>
                                    </Link>
                                </li>
                                <li>
                                    <Link to="/react-gh-pages/project4">
                                        <span className="solid">Project 4</span>
                                    </Link>
                                </li>
                            </ul>
                        </nav>
                    </div>

                    <div className="bottom">
                        <ul className="icons">
                            <li><a href="#" className="icon brands fa-twitter"><span className="label">Twitter</span></a></li>
                            <li><a href="#" className="icon brands fa-facebook-f"><span className="label">Facebook</span></a></li>
                            <li><a href="#" className="icon brands fa-github"><span className="label">Github</span></a></li>
                            <li><a href="#" className="icon brands fa-dribbble"><span className="label">Dribbble</span></a></li>
                            <li><a href="#" className="icon solid fa-envelope"><span className="label">Email</span></a></li>
                        </ul>
                    </div>

                </div>

                <div id="main">



                    <section id="portfolio" class="two">
                        <div class="container">

                            <header>
                                <h2>GPU Calculated Noise with Multi-threaded Mesh Generation in Unity</h2>
                            </header>

                            <p>This project was produced with C# and HLSL. The terrain is infinite in all directions and generates in chunks, which are computed in parrelell on the GPU. Pointers to voxel data are stored in a hash table and meshes are calculated on another thread. I'm using the open source library, FastNoiseLite. I have included some of the source code below the images, check it out!.  </p>

                        </div>
                    </section>

                    <img src={voxels1} className = "center" alt="voxel 3D noise" />
                    <img src={voxels2} className = "center" alt="voxel 3D noise" />
                    <img src={voxels3} className = "center" alt="voxel 3D noise" />


                    <div>
                        <a href={chunksManagerScript}> Chunk Management </a>
                        <br />
                        <a href={noiseComputeShader}> Compute Shader </a>
                        <br />
                        <a href={chunkScript}> Chunk class </a>
                    </div>

                </div>

            </body>

            <GoToTop/>

        </>
    )
}

export default Project1;