import React from "react";
import { Link } from "react-router-dom";
import avatar from "../../images/avatar.jpg";
import { Link as LinkScroll, animateScroll as scroll } from "react-scroll";

import GoToTop from '../shared/GoToTop.js';

import mathLibrary from "./MathLibrary.PNG";
import TristansLibrary from "./TristansLibrary.hpp";

const Project4 = () => {
    return (
        <>
            <body class="is-preload">

                <div id="header">
                    <div className="top">

                        <div id="logo">
                            <span className="image avatar48"><img src={avatar} alt="" /></span>
                            <Link to="/react-gh-pages/">
                                <h1 id="title">Tristan Johnson</h1>
                            </Link>
                            <p>Software Developer</p>

                        </div>

                        <nav id="nav">
                            <ul>
                                <li>
                                    <Link to="/react-gh-pages/">
                                        <span className="solid">Home</span>
                                    </Link>
                                </li>
                                <li>
                                    <Link to="/react-gh-pages/project1">
                                        <span className="solid">Project 1</span>
                                    </Link>
                                </li>
                                <li>
                                    <Link to="/react-gh-pages/project2">
                                        <span className="solid">Project 2</span>
                                    </Link>
                                </li>
                                <li>
                                    <Link to="/react-gh-pages/project3">
                                        <span className="solid">Project 3</span>
                                    </Link>
                                </li>
                            </ul>
                        </nav>
                    </div>

                    <div className="bottom">
                        <ul className="icons">
                            <li><a href="#" className="icon brands fa-twitter"><span className="label">Twitter</span></a></li>
                            <li><a href="#" className="icon brands fa-facebook-f"><span className="label">Facebook</span></a></li>
                            <li><a href="#" className="icon brands fa-github"><span className="label">Github</span></a></li>
                            <li><a href="#" className="icon brands fa-dribbble"><span className="label">Dribbble</span></a></li>
                            <li><a href="#" className="icon solid fa-envelope"><span className="label">Email</span></a></li>
                        </ul>
                    </div>

                </div>

                <div id="main">



                    <section id="portfolio" class="two">
                        <div class="container">

                            <header>
                                <h2>Simple Math Library in C++</h2>
                            </header>

                            <p>This library contains many useful functions and classes, such as vector transforms, random number generators, and several functions optimized to using only integers that you commonly don't see in other math libraries.</p>

                            <p>Below is an example of a function in the library. It transforms a 3D vector from local space to world space relative to a given transform. The full src code is available below and on my Github account. </p>

                        </div>
                    </section>


                    <div>
                            <img src = {mathLibrary}className = "center"/>
                    </div>

                    <a href = {TristansLibrary}>Download full library here</a>
                </div>

            </body>
            <GoToTop/>

        </>
    )
}

export default Project4;