import React from "react";
import { Link } from "react-router-dom";
import avatar from "../../images/avatar.jpg";
import { Link as LinkScroll, animateScroll as scroll } from "react-scroll";

import GoToTop from '../shared/GoToTop.js';

import voxelRayCasting1 from "./VoxelRaycasting1.PNG";
import voxelRayCasting2 from "./VoxelRaycasting2.PNG";
import voxelRayCasting3 from "./VoxelRaycasting3.PNG";

import rayCastingShader from "./RayTracing3.compute"

const Project3 = () => {
    return (
        <>
            <body class="is-preload">

                <div id="header">
                    <div className="top">

                        <div id="logo">
                            <span className="image avatar48"><img src={avatar} alt="" /></span>
                            <Link to="/react-gh-pages/">
                                <h1 id="title">Tristan Johnson</h1>
                            </Link>
                            <p>Software Developer</p>

                        </div>

                        <nav id="nav">
                            <ul>
                                <li>
                                    <Link to="/react-gh-pages/">
                                        <span className="solid">Home</span>
                                    </Link>
                                </li>
                                <li>
                                    <Link to="/react-gh-pages/project1">
                                        <span className="solid">Project 1</span>
                                    </Link>
                                </li>
                                <li>
                                    <Link to="/react-gh-pages/project2">
                                        <span className="solid">Project 2</span>
                                    </Link>
                                </li>
                                <li>
                                    <Link to="/react-gh-pages/project4">
                                        <span className="solid">Project 4</span>
                                    </Link>
                                </li>
                            </ul>
                        </nav>
                    </div>

                    <div className="bottom">
                        <ul className="icons">
                            <li><a href="#" className="icon brands fa-twitter"><span className="label">Twitter</span></a></li>
                            <li><a href="#" className="icon brands fa-facebook-f"><span className="label">Facebook</span></a></li>
                            <li><a href="#" className="icon brands fa-github"><span className="label">Github</span></a></li>
                            <li><a href="#" className="icon brands fa-dribbble"><span className="label">Dribbble</span></a></li>
                            <li><a href="#" className="icon solid fa-envelope"><span className="label">Email</span></a></li>
                        </ul>
                    </div>

                </div>

                <div id="main">



                    <section id="portfolio" class="two">
                        <div class="container">

                            <header>
                                <h2>Raycasting Voxels in a Compute Shader</h2>
                            </header>

                            <p>In this project I am using Unity to render to a texture, and computing the pixels by marching rays along a grid of integer points in an HLSL compute shader.</p>

                        </div>
                    </section>


                    <div>
                            <img src = {voxelRayCasting1} className = "center" alt="raycasting voxels"/>
                            <img src = {voxelRayCasting2} className = "center" alt="raycasting voxels"/>
                            <img src = {voxelRayCasting3} className = "center" alt="raycasting voxels"/>
                    </div>

                    <a href = {rayCastingShader}>The Raycasting Compute Shader</a>

                </div>

            </body>
            <GoToTop/>

        </>
    )
}

export default Project3;